import axios from 'axios';

const url = 'api/items';

class ItemService {
    // get Items
    static getItems() {
        return axios.get(url);
    }
    // save Items
    static insertItem(params) {
        return axios.post(url, params);
    }
    // delete Items
    static deleteItem(id) {
        return axios.delete(`${url}${id}`)
    }
}

export default ItemService;