<script>
import ItemService from '../../ItemService';

export default {
  name: 'HelloWorld',
  data() {
    return {
      posts: [],
      error: '',
      text: '',
    };
  },
  created() {
    this.loadAllPosts();
  },
  methods: {
    loadAllPosts() {
      ItemService.getItems()
      .then(({ data }) => {
        console.log(data);
        this.posts = data.map(item => {
          return {
            name: item.name,
            _id: item._id,
            createdAt: new Date(),
          };
        })
      })
      .catch((error) => {
        this.error = error;
      })
    },
  },
}
</script>

<template>
  <div class="container">
    <h1>Latest Items</h1>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="posts-container">
      <div class="post"
           v-for="(post,index) in posts"
           :key="post._id"
           :item="post"
           :index="index"
      >
        {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}/
        ${post.createdAt.getFullYear()}` }}
        <p class="text">
          {{ post.name }}
        </p>
      </div>
    </div>
  </div>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div.container { max-width: 800px; margin: 0 auto; }

p.error { border: 1px solid #ff5b5f; background-color: #ffc5c1; padding: 10px; margin-bottom: 15px; }

div.post { position: relative; border: 1px solid #5bd658; background-color: 3bcffb8; padding: 10px 10px 30px 10px; margin-bottom: 15px; }

div.created-at { position: absolute; top: 0; left: 0; padding: 5px 15px 5px 15px; background-color: darkgreen; }

p.text { font-size: 22px; font-weight: 700; margin-bottom: 0; }
</style>
